<template>
  <div class="container">
    <div class="box-one">
      <div class="one-title">{{ detailData.ruleName }}</div>
      <div class="one-arrange">
        <el-row>
          <el-col :span="6">
            <div class="arrange-title">{{ $t('rule.triggerDate') }}</div>
            <div class="arrange-box">{{ detailData.createTime }}</div></el-col
          >
          <el-col :span="6">
            <div class="arrange-title">{{ $t('rule.createUser') }}</div>
            <div class="arrange-box">{{ detailData.createStaffName }}</div></el-col
          >
          <el-col :span="6">
            <div class="arrange-title">{{ $t('rule.supplier') }}</div>
            <div class="arrange-box">{{ detailData.supplierNbrDesc }}</div></el-col
          >
          <el-col :span="6">
            <div class="arrange-title">{{ $t('rule.iotNetworkCard') }}</div>
            <div class="arrange-box">{{ detailData.msisdn }}</div></el-col
          >
        </el-row>
      </div>
    </div>
    <div class="box-two">
      <div class="two-title">{{ $t('rule.ruleDefine') }}</div>
      <div class="two-arrange">
        <el-row>
          <el-col v-for="(item, index) in defineList" :key="index" :span="6">
            <div class="arrange-title">{{ item.name }}</div>
            <div class="arrange-box">
              {{ item.formatter() }}
            </div></el-col
          >
        </el-row>
      </div>
    </div>
    <div class="box-two">
      <div class="two-title">{{ $t('rule.pushContent') }}</div>
      <div class="two-value">
        {{ detailData.msgInfo }}
      </div>
    </div>
    <div class="box-two">
      <div class="two-title">{{ $t('rule.operationResInfo') }}</div>
      <div class="two-arrange">
        <el-row>
          <el-col :span="6">
            <div class="arrange-title">{{ $t('rule.operationRes') }}</div>
            <div class="arrange-box">
              {{ formatSendState(detailData.sendState) }}
            </div></el-col
          >
          <el-col :span="6">
            <div class="arrange-title">{{ $t('rule.failureReason') }}</div>
            <div class="arrange-box">
              {{ detailData.errInfo ? detailData.errInfo : '-' }}
            </div></el-col
          >
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
import {
  i18nKeyFromSubType,
  i18nKeyFromRuleType,
  i18nKeyFromObjType,
  i18nKeyFromSendState,
} from '../constant'
export default {
  data() {
    return {
      detailData: {},
      defineList: [],
    }
  },
  computed: {
    resultList() {
      return [
        {
          name: this.$t('rule.operationRes'),
          value: 'sendStateDesc',
        },
        {
          name: this.$t('rule.failureReason'),
          value: 'errInfo',
        },
      ]
    },
  },
  mounted() {
    this.queryRuleRecordDetail()
  },
  methods: {
    queryRuleRecordDetail() {
      const params = this.$route.query
      this.$api.rule.queryRuleRecordDetail(params).then((res) => {
        this.detailData = res
        this.configDefineList()
      })
    },
    formatSendState(sendState) {
      return this.$t(i18nKeyFromSendState(sendState))
    },
    configDefineList() {
      this.defineList = [
        {
          name: this.$t('rule.ruleCategory'),
          formatter: () => {
            return (
              this.$t(i18nKeyFromRuleType(this.detailData.ruleType)) ||
              this.detailData.ruleTypeName
            )
          },
        },
        {
          name: this.$t('rule.ruleType'),
          formatter: () => {
            return (
              this.$t(i18nKeyFromSubType(this.detailData.ruleSubType)) ||
              this.detailData.ruleSubTypeName
            )
          },
        },
        {
          name: this.$t('rule.objectType'),
          formatter: () => {
            return (
              this.$t(i18nKeyFromObjType(this.detailData.objType)) ||
              this.detailData.objTypeName
            )
          },
        },
        {
          name: this.$t('rule.operate'),
          formatter: () =>
            this.$t('rule.sendMessageUrl') + ' ' + this.detailData.actionDest,
        },
      ]
    },
  },
}
</script>
